.header {
  height: 80vh;
  width: 100%;
  background-image: url("../img/header_bg.JPG");
  background-size: cover;
  background-repeat: no-repeat;
  display: block;
  overflow: hidden;
  font-family: "Mali", sans-serif;
}

.header h3 {
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  font-size: 1.40em;
  margin: 0;
}

header nav {
  float: right;
  line-height: 100px;
}

header nav a {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  padding-top: 30px;
  padding-right: 30px;
  padding-bottom: 30px;
  padding-left: 30px;
  line-height: normal;
  -webkit-transition: all 600ms ease;
  -o-transition: all 600ms ease;
  transition: all 600ms ease;
}

header nav a:hover {
  background: #d7bde2;
  border-radius: 40px;
  text-decoration: none;
  color: #fff;
}

.header-content {
  position: absolute;
  z-index: 1;
  top: 10%;
  left: 50%;
  margin-top: 105px;
  margin-left: -145px;
  width: 300px;
  height: 100px;
  text-align: center;
  color: #fff;
}

.title {
  /* font-family: "Oswald", sans-serif; */
  font-family: "Mali", sans-serif;
  /* font-family: 'Carter One', sans-serif ; */
  font-size: 3.5rem;
  padding-top: 3rem;
  padding-bottom: 3rem;
}

.skin {
  width: 100%;
  /* max-height: 100px; */
  overflow: hidden;
  position: absolute;
  margin: auto;
}
